import React,{Component} from 'react'
import { hashHistory} from 'react-router';
import { Form, Input, Button, Radio, Spin, message } from 'antd';
const FormItem = Form.Item;
const RadioGroup = Radio.Group;
const { TextArea } = Input;
export class DataForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            disabled: props.type == "detail" ? true : false,
            title: "",
            source: "",
            content: "",
            loading: true
        };
        ['handleSubmit', 'resetForm'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentDidMount() {
        let that = this;
        if(this.props.type == "add") {
            this.setState({loading: false});
            that.KE = window.K.create('#content', {
                allowFileManager : true,
                afterChange: function() {
                    that.props.form.setFieldsValue({"content" :this.html()});
                }
            });
        } else {
            ajax({
                url: "/platform/report/getInfoDetail",
                method: "post",
                data: {
                    id: this.props.id
                },
                callback: function(data) {
                    if(data.status == 200) {
                        let { title, source, content} = data.data.detailInfo;
                        that.setState({
                            loading: false,
                            title,
                            source,
                            content
                        });
                        that.KE = K.create('#content', {
                            allowFileManager : true,
                            afterChange: function() {
                                that.props.form.setFieldsValue({"content" :this.html()});
                            }
                        });
                        that.KE.html(content);
                        if(that.state.disabled) {
                            that.KE.readonly();
                            $(".anticon-delete").remove();

                        }
                    } else {
                        message.error(data.errMsg, 1);
                    }
                }
            });
        }
    }

    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                if(this.props.type == "edit") {
                    values = Object.assign(values, {id: this.props.id});
                }
                ajax({
                    url: "/platform/report/updateInfo",
                    data: values,
                    callback: function(data) {
                        if(data.status == 200) {
                            message.success("设置成功!", 1, function() {
                                hashHistory.push("/platform/report")
                            });
                        } else {
                            message.error(data.errMsg, 1)
                        }
                    }
                });
            }
        });
    }

    returnList() {
        hashHistory.push("/platform/report");
    }

    resetForm() {
        this.props.form.resetFields();
        this.KE.html(this.props.form.getFieldValue("content"));
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 6 },
            wrapperCol: { span: 6 }
        };
        return (
            <Spin spinning={this.state.loading} tip="Loading...">
                <Form>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="标题"
                        hasFeedback
                    >
                        {getFieldDecorator('title', {
                            rules: [
                                { required: true, message: '请输入标题!' }
                            ],
                            initialValue: this.state.title
                        })(
                            <Input disabled={this.state.disabled} type="text" />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="来源"
                        hasFeedback
                    >
                        {getFieldDecorator('source', {
                            rules: [
                                { required: true, message: '请输入来源!' }
                            ],
                            initialValue: this.state.source
                        })(
                            <Input disabled={this.state.disabled} type="text" />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="内容"
                        hasFeedback
                    >
                        {getFieldDecorator('content', {
                            rules: [
                                {
                                    required: true,
                                    message: "请输入内容!"
                                }
                            ],
                            initialValue: this.state.content
                        })(
                            <TextArea disabled={this.state.disabled} placeholder="这里输入内容" autosize={{ minRows: 6 }} />
                        )}
                    </FormItem>
                    <FormItem
                        wrapperCol={{ span: 12, offset: 6 }}
                    >
                        { !this.state.disabled ? <Button type="primary" onClick={this.handleSubmit} style={{"marginRight": "20px"}}>提交</Button> : ""}

                        <Button onClick={this.returnList} style={{"marginRight": "20px"}}>取消</Button>
                        { !this.state.disabled ? <Button type="danger" onClick={this.resetForm}>重置</Button> : ""}
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedDataForm = Form.create({})(DataForm);

module.exports = WrappedDataForm;


