import React,{Component} from 'react'
import { hashHistory } from 'react-router';
import { Layout, Breadcrumb, Button, Table, Popconfirm, message, Spin } from 'antd';
const { Content } = Layout;

class Detail extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: true,
            columns: [{
                title: "举报人",
                render: data =>
                    <div className="divClick" onClick={this.goToUserDetail.bind(this, data.reporter.id)}>{data.reporter.name}</div>
            }, {
                title: "举报内容",
                dataIndex: "content"
            }, {
                title: "举报日期",
                dataIndex: "reportTime"
            }, {
                title: "操作",
                render: data =>
                    <div style={{textAlign: "center"}}>
                        <Popconfirm placement="left" title="是否确认删除?" onConfirm={this.deleteReportDetail.bind(this, data.id)} okText="确认" cancelText="取消">
                            <Button style={{marginLeft:20}} type="danger">删除</Button>
                        </Popconfirm>
                    </div>

            }],
            dataSource: [],
            pagination: {
                current: 1,
                total: 0
            }
        };
        ['loadListData', 'changePage', 'deleteReportDetail'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentWillMount() {
        this.loadListData(1);
    }

    goToUserDetail(id) {
        hashHistory.push("/user/manage/detail/" + id);
    }

    changePage(page) {
        this.loadListData(page.current);
    }

    deleteReportDetail(id) {
        let that = this;
        ajax({
            url: "/report/detail/deleteInfo",
            data: {
                id: id
            },
            callback: function(data) {
                if(data.status == 200) {
                    message.success("删除成功!", 1);
                    let newDataSource = that.state.dataSource.filter(item => { return item.id != id});
                    that.setState({dataSource: newDataSource});
                } else {
                    message.error(data.errMsg, 1);
                }
            }
        })
    }

    loadListData(page) {
        let that = this;
        ajax({
            url: "/report/getInfoDetail",
            method: "post",
            data:  {
                id: this.props.params.id,
                current: page
            },
            callback: function(data) {
                if(data.status == 200) {
                    that.setState({
                        pagination: {
                            current: data.data.current,
                            total: data.data.total
                        },
                        dataSource: data.data.detailInfo,
                        loading: false
                    })
                } else {
                    message.error(data.errMsg, 1);
                }
            }
        })
    }

    return() {
        history.back();
    }

    render() {
        return (
            <Layout style={{ padding: '0 24px 24px' }}>
                <Breadcrumb style={{ margin: '12px 0' }}>
                    {
                        this.props.routes.map(function(item, index) {
                            return <Breadcrumb.Item key="">{item.breadcrumbName}</Breadcrumb.Item>
                        })
                    }
                </Breadcrumb>
                <Content style={{ background: '#fff', padding: 24, margin: 0, minHeight: 280 }}>
                    <Spin spinning={this.state.loading} tip="Loading...">
                        <Button style={{marginBottom: 30}} icon="rollback" onClick={this.return}>返回</Button>
                        <Table
                            bordered
                            dataSource={this.state.dataSource}
                            columns={this.state.columns}
                            pagination={this.state.pagination}
                            onChange={this.changePage}
                        />
                    </Spin>
                </Content>
            </Layout>
        )
    }

}

module.exports = Detail;


